.navbarContainer {
    height: 60px;
    background-color: #EBE0E9;
    display: flex;
    align-items: center;
    filter: drop-shadow(0px 4px 14px rgba(45, 46, 70, 0.15));
}

.title {
    color: #2D2E46;
    margin: 0 40px;
    font-size: 21px;
}

.navbarContainer a {
    padding-right: 3px;
    text-decoration: none;
    color: #333;
    margin-right: 10px;
    transition-delay: 0.1s;
    border-right: 1px solid #333;
}

.navbarContainer a:hover {
    color: mediumslateblue;
    border-color: mediumslateblue;
}

.navbarContainer a.active {
    color: mediumslateblue;
    border-color: mediumslateblue;
}